<template>
	<div v-if="this.$store.state.userData">
		<h1>Hello {{ this.$store.state.userData.username }}</h1>

		<div class="card">
			<h2>My Plekollections</h2>
			<div
				v-for="(p, i) in this.$store.state.userData.my_plekollections"
				class="list"
			>
				{{ p.name }}
			</div>
			<br />
		</div>
        <div class="card">
			<h2>My Plekjes</h2>
			<div
				v-for="(p, i) in this.$store.state.userData.my_plekjes"
				class="list"
			>
				{{ p.name }}
			</div>
			<br />
		</div>
	</div>
</template>

<script>
var api = require("@/service");
export default {
	methods: {
		getUserProfile() {
			const query = JSON.stringify({
				email: this.$store.state.userEmail,
			});
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL + "/users/get_one",
				query,
				(e) => {
					let mongodata = JSON.parse(e);
					if (!mongodata) {
						this.$store.state.userToBeCreated = true;
					} else {
						this.$store.commit("setUserData", mongodata);
					}
				}
			);
		},
	},
	mounted() {
		this.getUserProfile();
	},
};
</script>

