<template>
	<br />
	<span>Choose a Username: </span>
	<br />
	<input v-model="userName" />
	<br />
	<button class="btn" @click="checkUserNameAvailability()">Save</button>

	<div v-if="!userNameAvailable" id="warning">
		<br />
		This username is unavailable
	</div>
</template>

<script>
var api = require("@/service");
export default {
	data() {
		return {
			userName: "",
			userNameAvailable: true,
		};
	},
	methods: {
		checkUserNameAvailability() {
			const query = JSON.stringify({ username: this.userName });
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL + "/users/name_check",
				query,
				(e) => {
					this.userNameAvailable = e === "true";
					if (this.userNameAvailable) {
						this.registerUser();
					}
				}
			);
		},
		registerUser() {
			console.log("creating user");
			const newUser = {
				email: this.$store.state.userEmail,
				username: this.userName,
				my_plekjes: [],
				my_plekollections: [],
			};
			this.userName = "";
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL + "/users/new",
				JSON.stringify(newUser),
				(e) => {
					this.$store.state.userToBeCreated = false;
				}
			);
		},
	},
};
</script>

<style scoped>
span {
	font-size: 21px;
	text-align: left;
}

input {
	padding: 5px 10px;
	font-size: 21px;
	margin: 10px;
}
#warning {
	color: #dc3545;
}
</style>