<template>
	<div v-if="this.$store.state.userToBeCreated">
        <UserCreate/>
    </div>
	<div v-else-if="this.$store.state.userEmail.length !== 0">
        <UserData/>
    </div>
    <div v-else><p>Loading profile...</p></div>
</template>

<script>
import UserCreate from "@/components/UserCreate";
import UserData from "@/components/UserData";

export default {
    components:{
        UserCreate,
        UserData
    },
    data() {
        return {
        };
    },
    mounted() {
        if (this.$store.state.userEmail.length === 0) {
            this.$store.dispatch("auth0getUserEmail");
        }
    }
};
</script>

